<template>
  <div class="comleftNav">
     <el-menu
      default-active="/"
      router
      class="el-menu-vertical-demo"
      background-color="#4d83e7"
      text-color="#fff"
      active-background-color="#409EFF"
      :default-openeds="['company', 'CM', 'statistics']">
      <el-submenu index="company">
        <template slot="title">
          <i class="icon iconfont icon-gerenziliao"></i>
          <span class="fontBig"></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="/com/company-comAchi"></el-menu-item>
        <el-menu-item index="/com/uploadAchieve"></el-menu-item>          
          <el-menu-item index="/com/company-register" data-step="2" data-intro="" data-position="right"></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
     <el-submenu index="CM">
        <template slot="title">
          <i class="icon iconfont icon-guanli"></i>
          <span class="fontBig"></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="/com/PM-new" data-step="1" data-intro="" data-position="right"></el-menu-item>
          <el-menu-item index="/com/CM-sumary/2" data-step="4" data-intro="" data-position="bottom"></el-menu-item>
          <el-menu-item index="/com/CM-combo/2"></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
        <el-submenu index="statistics">
        <template slot="title">
          <i class="icon iconfont icon-tongji"></i>
          <span class="fontBig"></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="/com/Cstatistics-time"></el-menu-item>
          <el-menu-item index="/com/Cstatistics-discuss"></el-menu-item>
          <el-menu-item index="/com/Cstatistics-credit"></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.icon{
    color:#fff;
    margin-left:-1.5rem;
    font-size:1.5rem;
}
span{
  font-size:1rem;
}
.fontBig{
  font-size:1.3rem;
  font-weight:100;
}
</style>



