<template>
  <div>
    <div class="content">
      <p style="text-align:left;width:100%"></p>
      <div style="margin:1rem auto">
        <el-button type="primary" @click="dialogVisible=true"></el-button>
        <el-button type="primary" @click="dialogVisible2=true"></el-button>
      </div>
      <el-dialog title="" :visible.sync="dialogVisible" width="30%">
        <span>ckjxxx</span>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
      <el-dialog title="" :visible.sync="dialogVisible2" width="60%">
        <el-table :data="table">
          <el-table-column prop="account" label=""></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="sex" label=""></el-table-column>
          <el-table-column prop="telephone" label=""></el-table-column>
          <el-table-column prop="email" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click="skip({'account':scope.row.account, 'name':scope.row.name})" type="primary" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span> -->
      </el-dialog>      
      <p style="text-align:left;width:100%"></p>
      <div style="margin:1rem auto">
        <el-button type="primary" @click="$router.push('/per/PM-new/2')"></el-button>
      </div>
    </div>
    <div class="skip">
      <div style="margin-left:2rem" @click="$router.push('/per/PM-new')"><i class="icon iconfont icon-zuo"></i><span></span></div>
      <div style="margin-right:2rem" @click="$router.push('/per/PM-new/3')"><span></span><i class="icon iconfont icon-you"></i></div>
    </div>
  </div>
</template>

<script>
import store from '@/vuex/step.js'
import {mapState, mapMutations} from 'vuex'
export default {
  store,
  computed: {
    ...mapState(['step', 'form'])
  },
  data () {
    return {
      dialogVisible: false,
      dialogVisible2: false,
      table: []
    }
  },
  methods: {
    skip (row) {
      this.$router.push('/chat/contact')
      this.$nextTick(() => {
        this.$one.$emit('skipChat', row)
      })
    },
    initData () {
      var formData = {'conditions': {'jobs': {'searchMethod': 'values', 'values': ['']}}}
      this.$http.post(this.$domain.domain1 + 'electric-design/getPusersByMultiConditions', formData)
        .then((res) => {
          console.log(res.data)
          this.table = res.data
        }).catch((err) => {
          console.log(err)
        })
    },
    ...mapMutations(['init2'])
  },
  mounted () {
    this.initData()
    this.init2()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.skip{
  margin-top:4rem;
  display:flex;
  justify-content:space-between;
  align-items:center;
  color:#4d83e7;
  font-weight:500;
}
.content{
  margin:3rem auto 0 auto;
  width:80%;
}
</style>



