<template>
  <div class="person-register">
    <div class="title"><span style="font-size:2rem"></span><i class="icon iconfont icon-iconfontquestion"></i></div>
    <div style="margin-top:3rem">
      <p style="text-align:left"></p>
      <p style="text-align:left"></p>
      <p style="text-align:left"></p>
    </div>
    <el-row style="margin-top:2rem">
      <el-col :span="11" class="shadow">
        <el-form :label-position="labelPosition" label-width="120px" :model="ruleForm">
          <el-form-item label=""  prop="name">
            <el-input v-model="ruleForm.name" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-radio-group v-model="ruleForm.sex">
              <el-radio label="" disabled></el-radio>
              <el-radio label="" disabled></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-col :span="24">
              <el-select v-model="ruleForm.age" placeholder="" style="width:100%" disabled>
              <el-option v-for="item in Ages" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="ruleForm.workUnit" style="width:100%"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="ruleForm.graduateInstitution" style="width:100%"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="ruleForm.workType" style="width:100%">
              <el-option v-for="(i, j) in workType" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-form-item>                       
          <el-form-item label="">
            <el-select v-model="ruleForm.acceptableTravelTime" style="width:100%" filterable allow-create default-first-option>
              <el-option v-for="(i, j) in acceptableTravelTime" :key="j" :label="i" :value="i"></el-option>             
            </el-select>
          </el-form-item>   
          <el-form-item label="" prop="telephone">
            <el-input v-model="ruleForm.telephone" style="width:100%"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="ruleForm.email" style="width:100%"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select style="width:100%" v-model="ruleForm.job" filterable allow-create default-first-option multiple>
              <el-option v-for="(i, j) in jobs" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select style="width:100%" v-model="ruleForm.major" multiple filterable allow-create default-first-option>
              <el-option v-for="(i, j) in major" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-form-item>             
          <el-form-item label="" prop="place">
            <div style="display:flex;justify-content:space-around;margin-left:0px">
            <el-select v-model="ruleForm.prov" placeholder="" v-on:change="getProv($event)" style="width:30%;margin-right:0.6rem;">
              <el-option v-for="item in provs" :key="item.name" :label="item.name" :value="item.name"></el-option>
            </el-select>
            <el-select v-model="ruleForm.city" placeholder="" v-on:change="getCity($event)" style="width:30%;margin-right:0.6rem;">
              <el-option v-for="item in cities" :key="item.name" :label="item.name" :value="item.name"></el-option>
            </el-select>
            <el-select v-model="ruleForm.area" placeholder="" style="width:30%">
              <el-option v-for="item in areas" :key="item" :label="item" :value="item"></el-option>
            </el-select>            
            </div>
          </el-form-item>
          <el-form-item label="">
              <el-radio v-model="radio" label="1"></el-radio>
              <el-radio v-model="radio" label="2"></el-radio>
          </el-form-item>
          <el-form-item label="" v-if="radio==='2'" v-for="(i, j) in workingAddress" :key="j">
            <div style="display:flex;justify-content:space-around;margin-left:0px">
            <el-select v-model="i.prov" placeholder="" v-on:change="getProv($event)" style="width:25%;margin-right:0.6rem;">
              <el-option v-for="item in provs" :key="item.name" :label="item.name" :value="item.name"></el-option>
            </el-select>
            <el-select v-model="i.city" placeholder="" v-on:change="getCity($event)" style="width:25%;margin-right:0.6rem;">
              <el-option v-for="item in cities" :key="item.name" :label="item.name" :value="item.name"></el-option>
            </el-select>
            <el-select v-model="i.area" placeholder="" style="width:25%">
              <el-option v-for="item in areas" :key="item" :label="item" :value="item"></el-option>
            </el-select>            
            <i class="icon iconfont icon-cha" @click="deleteWorkingAddress(j)"></i>
            </div>
          </el-form-item>
          <el-form-item label="" v-if="radio==='2'"><el-button type="info" @click="addWorkingAddress()" style="width:100%"></el-button></el-form-item>
          <el-form-item><el-button type="primary" @click="submit()" style="width:120%;margin-left:-120px;"></el-button></el-form-item>
        </el-form>
      </el-col>
      <el-col :span="11" :offset="2" class="shadow">
        <div class="uploading">
          <p style="text-align:left;font-size:1.5rem"></p>
          <el-progress :text-inside="true" :stroke-width="18" :percentage="complete" :status="status" style="margin-top:2rem"></el-progress>
          <p style="text-align:left;font-size:1rem;margin-top:3rem;"></p>
          <div class="div1-style">
            <span></span>
            <el-upload class="upload-demo" :action='$domain.domain1 + "electric-design/uploadUsersDatas"' :data="{'userDatatype': 'education', 'sourceType': $cookie.get('role'), 'sourceName': $cookie.get('name'), 'sourceAccount': $cookie.get('user')}" multiple name="data" :on-success="success" :on-error="failure" :on-exceed="handleExceed"  :limit="3">
            <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
          <div class="div1-style">
            <span></span>
            <el-upload class="upload-demo" :action='$domain.domain1 + "electric-design/uploadUsersDatas"' :data="{'userDatatype': 'idNumber', 'sourceType': $cookie.get('role'), 'sourceName': $cookie.get('name'), 'sourceAccount': $cookie.get('user')}" name="data" :on-success="success" :on-error="failure" :on-exceed="handleExceed"  :limit="3">
            <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
          <div class="div1-style">
            <span></span>
            <el-upload class="upload-demo" :action='$domain.domain1 + "electric-design/uploadUsersDatas"' :data="{'userDatatype': 'work', 'sourceType': $cookie.get('role'), 'sourceName': $cookie.get('name'), 'sourceAccount': $cookie.get('user')}" multiple name="data" :on-success="success" :on-error="failure" :on-exceed="handleExceed"  :limit="3">
            <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
          <div style="margin-top:3rem">
            <span style="color:#909399;font-size:1.5rem"></span>
            <el-input type="textarea" v-model="ruleForm.instruction" :autosize="{ minRows: 16, maxRows: 18}" placeholder=""></el-input>
            <el-button type="success" @click="submitInstruction()"></el-button>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>

export default {
  data () {
    return {
      radio: '1',
      complete: 100,
      status: 'success',
      Ages: [],
      workType: ['', ''],
      jobs: ['', '', '', '', '', '', '', ''],
      acceptableTravelTime: ['', '', '', ''],
      labelPosition: 'right',
      major: [],
      workingAddress: [
        { prov: '',
          city: '',
          area: '' }],
      ruleForm: {
        name: '',
        sex: '',
        age: '',
        workUnit: '',
        graduateInstitution: '',
        acceptableTravelTime: '',
        workingAddress: '',
        workType: '',
        telephone: '',
        email: '',
        job: [],
        prov: '',
        city: '',
        area: '',
        instruction: '',
        major: []
      },
      provs: [],
      cities: [],
      areas: []
    }
  },
  created () {
  },
  mounted () {
    this.initData()
  },
  methods: {
    initData () {
      this.$http.get(this.$domain.domain1 + 'electric-design/getTypeMajors').then((res) => {
        this.major = res.data.majors
      }).catch((err) => {
        console.log(err)
        this.$message({showClose: true,
          message: '',
          type: 'error'
        })
      })
      this.$http.post(this.$domain.domain1 + 'electric-design/getDataOfClassKey')
        .then((res) => {
          this.jobs = res.data.duties
        }).catch((err) => {
          console.log(err)
        })
      this.$http.post(this.$domain.domain1 + 'electric-design/getPuserByAccount', {'account': this.cookie.get('user')}).then((res) => {
        console.log(res.data)
        this.ruleForm.name = res.data.name
        this.ruleForm.sex = res.data.sex
        this.ruleForm.age = res.data.age
        this.ruleForm.telephone = res.data.telephone
        this.ruleForm.email = res.data.email
        this.ruleForm.job = Array.isArray(res.data.jobs) ? res.data.jobs : []
        this.ruleForm.major = Array.isArray(res.data.major) ? res.data.major : []
        this.ruleForm.workUnit = res.data.workUnit
        this.ruleForm.graduateInstitution = res.data.graduateInstitution
        this.ruleForm.acceptableTravelTime = res.data.acceptableTravelTime
        if (Array.isArray(res.data.workingAddress)) {
          this.radio = '2'
          this.workingAddress = []
          res.data.workingAddress.forEach((el, index) => {
            this.workingAddress.push({
              'prov': el.split('/')[0],
              'city': el.split('/')[1],
              'area': el.split('/')[2]
            })
          })
        } else {
          this.radio = '1'
        }
        this.ruleForm.workingAddress = res.data.workingAddress
        this.ruleForm.workType = res.data.workType
        this.ruleForm.prov = res.data.birthAddress.province
        this.ruleForm.city = res.data.birthAddress.city
        this.ruleForm.area = res.data.birthAddress.area
        this.ruleForm.instruction = res.data.instruction
        this.complete = 10
        for (var i in res.data.dataOfDeepth) {
          if (res.data.dataOfDeepth[i] === 'yes') { this.complete += 30 }
        }
      }).catch((err) => {
        console.log(err)
      })
      this.Ages = []
      for (let i = 18; i < 80; i++) {
        this.Ages.push({value: i, label: i})
      }
      this.$http.get(this.$domain.domain1 + 'electric-design/getAreasOfChina').then(res => {
        this.provs = res.data
      }).catch(err => {
        console.log(err)
        this.$message({showClose: true,
          message: '',
          type: 'error'
        })
      })
    },
    addWorkingAddress () {
      this.workingAddress.push({
        prov: '',
        city: '',
        area: ''
      })
    },
    deleteWorkingAddress (index) {
      this.workingAddress = this.workingAddress.filter(o => this.workingAddress.indexOf(o) !== index)
    },
    getProv (prov) {
      this.cities = []
      for (var val of this.provs) {
        if (prov === val.name) {
          this.cities = val.cityList
          this.prov = val.name
        }
      }
    },
    getCity (city) {
      this.areas = []
      for (var val of this.cities) {
        if (city === val.name) {
          this.areas = val.areaList
          this.city = val.name
        }
      }
    },
    submit () {
      if (this.radio === '1') {
        this.ruleForm.workingAddress = ''
      } else {
        // this.ruleForm.workingAddress = ''
        var arrTemp = []
        this.workingAddress.forEach((el, index) => {
          arrTemp[index] = `${el.prov}/${el.city}/${el.area}`
        })
        this.ruleForm.workingAddress = arrTemp
      }
      this.$http.post(this.$domain.domain1 + 'electric-design/changePuserByAccount',
        {
          'account': this.cookie.get('user'),
          'data': {
            'account': this.cookie.get('user'),
            'name': this.ruleForm.name,
            'sex': this.ruleForm.sex,
            'age': this.ruleForm.age,
            'telephone': this.ruleForm.telephone,
            'email': this.ruleForm.email,
            'workUnit': this.ruleForm.workUnit,
            'graduateInstitution': this.ruleForm.graduateInstitution,
            'acceptableTravelTime': this.ruleForm.acceptableTravelTime,
            'workingAddress': this.ruleForm.workingAddress,
            'jobs': this.ruleForm.job,
            'major': this.ruleForm.major,
            'workType': this.ruleForm.workType,
            'birthAddress': {
              'province': this.ruleForm.prov,
              'city': this.ruleForm.city,
              'area': this.ruleForm.area
            }}
        })
      .then((res) => {
        if (res.data.result) {
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'warning',
            message: `,${res.data.reason}`
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    submitInstruction () {
      this.$http.post(this.$domain.domain1 + 'electric-design/changePuserByAccount',
        {
          'account': this.cookie.get('user'),
          'data': {
            'account': this.cookie.get('user'),
            'instruction': this.ruleForm.instruction
          }
        })
      .then((res) => {
        if (res.data.result) {
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'warning',
            message: `,${res.data.reason}`
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    success (response, file, fileList) {
      console.log(response)
      // this.complete += 30
      // this.complete = response.data
      if (response.result) {
        this.$message({
          type: 'success',
          message: ``
        })
        this.initData()
      }
    },
    failure (err, file, fileList) {
      this.$message.warning(`${file.name}`)
      console.log(err)
    },
    handleExceed (files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.person-register{
  width:90%;
  margin: 1rem auto;
}
.title{
  border-bottom:1px solid black;
  text-align:left;
}
.icon{
  font-size:2rem;
  margin-left:1rem;
}
.el-col{
  background-color:white;
}
.el-form{
  margin:2rem 1rem;
}
.uploading{
  width:80%;
  margin:2rem auto;
}
.div1-style{
  margin-top:3rem;
  display:flex;
  justify-content:space-between;
}
.upload-demo{

}

.shadow{
 
  border-radius:.3rem;
  -webkit-box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 60px rgba(0, 0, 0, 0.1) inset;
  -moz-box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;

}
</style>



