<template>
  <div class="alter">
    <div class="title0"><span style="font-size:2rem"></span><i class="icon iconfont icon-iconfontquestion" style="font-size:2rem"></i></div>
    <div class="data" v-if="form.dataOrProject==='data'">
    <div class="title"><span></span><a :href="help1" class="help">?</a></div>
    <div class="content">
      <el-form ref="form" :model="form" label-width="80px" style="width:100%"> 
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.name"></el-input>
          </el-col>         
          <el-col :offset="5" :span="5"></el-col>
          <el-col :span="5">
           <el-switch v-model="form.isAcceptJointBid" active-text="" inactive-text="" inactive-value="false" active-value="true"></el-switch>
          </el-col>              
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.company"></el-input>
          </el-col>
          <el-col class="line" :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.categorys" placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in categorys" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.state"  placeholder="" style="width:100%">
            <el-option v-for="(i,j) in states" :key="j" :label="i.label" :value="i.value"></el-option>
          </el-select>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.designState" style="width:100%">
              <el-option v-for="(i, j) in designState" :key="j"  :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.type" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in type" :key="j" :label="i" :value="i"></el-option>
          </el-select>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.major" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in major" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-date-picker v-model="form.startTime" type="date" placeholder=""></el-date-picker>
          </el-col>          
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-date-picker v-model="form.endTime" type="date" placeholder=""></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-input  v-model="form.instruction" type="textarea" :autosize="{ minRows: 4}" placeholder=""></el-input>
        </el-form-item>        
        <el-form-item label="">
          <el-input  v-model="form.requirement" type="textarea" :autosize="{ minRows: 4}" placeholder=""></el-input>
        </el-form-item>                
      </el-form>
    </div>      
    <div class="title"><a :href="help3" class="help">?</a></div>
    <div class="content">
      <el-form :model="form" label-width="180px" style="width:100%">
        <el-form-item label="/">
          <el-col :span="8" style="display:flex;justify-content:space-between">
            <el-select v-model="form.paymentMethods" placeholder="" style="width:40%">
              <el-option v-for="(i,j) in paymentMethods" :key="j" :value="i" :label="i"></el-option>
            </el-select>
            <el-select v-model="form.paymentScale" placeholder="" style="width:40%" filterable allow-create default-first-option>
              <el-option v-for="(i,j) in paymentScale" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-switch v-model="form.hasInvoice" active-text="" inactive-text="" active-value="yes" inactive-value="no"></el-switch>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.payDiscible" type="textarea" :autosize="{ minRows: 4}" placeholder=""></el-input>
        </el-form-item>
      </el-form>
    </div>
    </div>    
    <div class="Project" v-else>
    <div class="title"><span></span><a :href="help1" class="help" >?</a></div>
    <div class="content">
      <el-form ref="form" :model="form" label-width="80px" style="width:100%"> 
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.company"></el-input>
          </el-col>
          <el-col :offset="5" :span="5"></el-col>
          <el-col :span="5">
           <el-switch v-model="form.isAcceptJointBid" active-text="" inactive-text="" inactive-value="false" active-value="true"></el-switch>
          </el-col>          
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.name"></el-input>
          </el-col>
          <el-col class="line" :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.categorys" placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in categorys" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="4">
            <el-select v-model="form.state"  placeholder="" >
            <el-option v-for="(i,j) in states" :key="j" :label="i.label" :value="i.value"></el-option>
          </el-select>
          </el-col>
          <el-col class="line" :span="1"></el-col>
          <el-col :span="5">
            <el-input v-model="form.sizeAndCapacitys" placeholder="" type="number"></el-input>
          </el-col>
          <el-col :span="3">
            <el-select v-model="form.unit"  placeholder="" >
            <el-option v-for="(i,j) in unit" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
          <el-col :offset="1" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.designState" style="width:100%">
              <el-option v-for="(i, j) in designState" :key="j"  :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.type" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in type" :key="j" :label="i" :value="i"></el-option>
          </el-select>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.major" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in major" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.voltagelevel1" style="width:30%">
              <el-option v-for="(i,j) in voltagetype" :key="j" :label="i" :value="i"></el-option>
            </el-select>
            <el-input v-model="form.voltagelevel2" style="width:35%" type="number"></el-input>
            <el-select v-model="form.voltagelevel3" placeholder="" style="width:30%">
              <el-option v-for="(i,j) in voltagelevel" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-date-picker v-model="form.startTime" type="date" placeholder=""></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.place"></el-input>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-date-picker v-model="form.endTime" type="date" placeholder=""></el-date-picker>
          </el-col>
        </el-form-item>
       <el-form-item label="">
          <el-col :span="9">
          <el-input v-model="form.lowPrice" placeholder="" style="width:40%;"></el-input>
          <span style="font-size:1.2rem;color:red"></span>
          <el-input v-model="form.highPrice" placeholder="" style="width:40%;"></el-input>
          </el-col>
       </el-form-item>        
        <el-form-item label="">
          <el-input  v-model="form.instruction" type="textarea" :autosize="{ minRows: 4}" placeholder=""></el-input>
        </el-form-item>        
        <el-form-item label="">
          <el-input  v-model="form.requirement" type="textarea" :autosize="{ minRows: 4}" placeholder=""></el-input>
        </el-form-item>          
        <el-button @click="confirm()" style="width:100%" type="success"></el-button>
      </el-form>
    </div>
    <div class="title"><span></span><a :href="help2" class="help">?</a></div>
    <div class="content">
      <el-form :model="form" label-width="140px" style="width:100%">
        <el-form-item label="">
          <el-col :span="9">
          <el-select v-model="form.character" placeholder="" style="width:100%">
            <el-option v-for="(i,j) in character" :key="j" :value="i" :label="i"></el-option>
          </el-select>
          </el-col>
          <el-col :offset="2" :span="3"></el-col>
          <el-col :span="4">
            <el-select v-model="form.performanceReq1" placeholder="" style="width:100%"  filterable allow-create default-first-option>
              <el-option v-for="(i,j) in performanceReq" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-col>
          <el-col :span="3"></el-col>
          <el-col :span="3">
            <el-input v-model="form.performanceReq2" style="width:100%"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="8">
            <el-input v-model="form.aptitude" placeholder="" style="width:100%"></el-input>
          </el-col>
        </el-form-item>
        <el-button @click="confirm()" style="width:100%" type="success"></el-button>
      </el-form>
    </div>
    <div class="title"><span></span><a :href="help3" class="help">?</a></div>
    <div class="content">
     <el-form :model="form" label-width="180px" style="width:100%">
        <el-form-item label="/">
          <el-col :span="8" style="display:flex;justify-content:space-between">
            <el-select v-model="form.paymentMethods" placeholder="" style="width:40%">
              <el-option v-for="(i,j) in paymentMethods" :key="j" :value="i" :label="i"></el-option>
            </el-select>
            <el-select v-model="form.paymentScale" placeholder="" style="width:40%" filterable allow-create default-first-option>
              <el-option v-for="(i,j) in paymentScale" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-switch v-model="form.hasInvoice" active-text="" inactive-text="" active-value="yes" inactive-value="no"></el-switch>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.payDiscible" type="textarea" :autosize="{ minRows: 4}" placeholder=""></el-input>
        </el-form-item>
      </el-form>
    </div>    
    <div class="title"><a :href="help4" class="help">?</a></div>
    <div class="content">
      <el-form :model="form" label-width="80px" style="width:100%">
        <el-radio v-model="radio" label="1"></el-radio>
        <el-radio v-model="radio" label="2"></el-radio>
        <el-table :data="form.stateUnits" stripe style="margin-top:2rem">
          <el-table-column label="">
            <template slot-scope="scope">
              <el-select v-model="scope.row.state">
                <el-option v-for="(i, j) in designState" :key="j" :label="i" :value="i"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-date-picker v-model="scope.row.endTime" :disabled="disabled" type="date" placeholder=""></el-date-picker>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-input type="textarea" autosize :disabled="disabled" v-model="scope.row.requireResult"></el-input>  
            </template>            
          </el-table-column>
          <el-table-column label="">
             <template slot-scope="scope">
               <el-button type="danger" @click="myDelete(scope.row)"><i class="icon iconfont icon-cha"></i>{{scope.row.state}}</el-button>
             </template>
          </el-table-column>
        </el-table>
        <el-button type="primary" @click="addStateUnits()" style="width:100%"></el-button>
      </el-form>
    </div>
    </div>
    <el-button @click="confirm()" style="width:100%" type="success"></el-button>
  </div>
</template>

<script>
import store from '@/vuex/perProject'
import {mapState} from 'vuex'
export default {
  store,
  computed: {
    ...mapState(['id'])
  },
  data () {
    return {
      changePNamePer: 'no',
      divideRolePer: 'no',
      disabled: false,
      radio: '2',
      form: {
        dataOrProject: '',
        company: '',
        name: '',
        state: '',
        sizeAndCapacitys: '',
        categorys: '',
        unit: '',
        type: [],
        voltagelevel1: '',
        voltagelevel2: 100,
        voltagelevel3: '',
        place: '',
        major: [],
        designState: '',
        startTime: '',
        endTime: '',
        character: '',
        performanceReq1: '',
        performanceReq2: '',
        lowPrice: '',
        highPrice: '',
        priceType: '',
        aptitude: '',
        paymentMethods: '',
        paymentScale: '',
        isAcceptJointBid: 'true',
        instruction: '',
        requirement: '',
        payDiscible: '',
        projectNowState: '',
        hasInvoice: 'no',
        stateUnits: [
      { state: '', endTime: '', requireResult: '' },
      { state: '', endTime: '', requireResult: '' },
      { state: '', endTime: '', requireResult: '' }
        ]
      },
      // qualificationRequirements: { CET: '' },
      projectCharacteristics: { difficulty: '', price: '' },
      unit: ['MW', 'Kva', 'KV', `M^2`],
      existingData: { detail: '' },
      states: [{ 'value': '', 'label': '' }, { 'value': '', 'label': '' }],
      major: ['', '', '', ''],
      designState: ['', '', '', '', '', '', ''],
      sizeAndCapacitys: ['MW', 'KW', 'kVA'],
      categorys: ['', '', '', '', '', ''],
      type: ['', '', '', '', ''],
      voltagelevel: ['KV', 'V'],
      voltagetype: ['', ''],
      character: ['', ''],
      performanceReq: ['', ''],
      priceType: ['', ''],
      aptitude: ['', ''],
      paymentMethods: ['', ''],
      paymentScale: ['1:1', '2:1', '3:1'],
      projectNowStates: ['', '', '', '', '', '']
    }
  },
  methods: {
    initPremiss () {
      this.$http.post(this.$domain.domain1 + 'electric-design/getPermessionsByMultiConditions', {
        'conditions': {
          'belongToProjectCode': {'searchMethod': 'values', 'values': [this.id]}
        }
      }).then((res) => {
        console.log(res.data)
        res.data.forEach((el, index) => {
          if (el.belongToUserId === this.$cookie.get('user')) {
            if (el.perContent.changePName === 'yes') {
              this.changePNamePer = 'yes'
            }
            if (el.perContent.divideRole === 'yes') {
              this.divideRolePer = 'yes'
            }
          }
        })
      }).catch((err) => {
        console.log(err)
      })
    },
    addStateUnits () {
      this.form.stateUnits.push({ state: '', endTime: '', requireResult: '' })
    },
    myDelete (el) {
      this.form.stateUnits = this.form.stateUnits.filter(o => o.state !== el.state)
    },
    initData () {
      this.$http.post(this.$domain.domain1 + 'electric-design/getProjectByCode', {'code': this.id})
      .then((res) => {
        console.log(res.data)
        this.form.company = res.data.tenderCompany
        this.form.dataOrProject = res.data.dataOrProject
        this.form.name = res.data.changeName
        this.form.categorys = Array.isArray(res.data.category) ? res.data.category[0] : res.data.category
        this.form.sizeAndCapacitys = res.data.sizeCapacityNumber
        this.form.unit = res.data.sizeAndCapacity
        this.form.character = res.data.bidType
        this.form.type = res.data.type
        this.form.voltagelevel1 = res.data.voltagelevel.split('/')[0]
        this.form.voltagelevel2 = res.data.voltagelevel.split('/')[1]
        this.form.voltagelevel3 = res.data.voltagelevel.split('/')[2]
        this.form.major = res.data.major
        this.form.place = res.data.address
        this.form.performanceReq1 = res.data.performanceRequirements.split('/:')[0]
        this.form.performanceReq2 = res.data.performanceRequirements.split('/:')[1]
        this.form.designState = res.data.designProcess
        this.form.lowPrice = res.data.lowestPrice
        this.form.highPrice = res.data.highPrice
        this.form.instruction = res.data.instruction
        this.form.requirement = res.data.requirement
        this.form.payDiscible = res.data.payDiscible
        this.form.projectNowState = res.data.projectNowState
        this.form.hasInvoice = res.data.hasInvoice
        this.form.aptitude = res.data.qualificationRequirements.toString()
        this.form.startTime = [].concat((res.data.startTime.year + 1900), (res.data.startTime.month + 1), res.data.startTime.date).join('/')
        this.form.endTime = [].concat((res.data.endTime.year + 1900), (res.data.endTime.month + 1), res.data.endTime.date).join('/')
        this.form.paymentMethods = res.data.payMethod.split('/')[0]
        this.form.paymentScale = res.data.payMethod.split('/')[1]
        this.form.isAcceptJointBid = res.data.isAcceptJointBid
        this.form.stateUnits = res.data.processRequirements
      }).catch((err) => {
        console.log(err)
      })
      this.$http.post(this.$domain.domain1 + 'electric-design/getDataOfClassKey').then((res) => {
        console.log(res.data)
        this.categorys = res.data.categorys
        this.type = res.data.types
        this.designState = res.data.designProcess
        this.unit = res.data.sizeAndCapacitys
        this.major = res.data.majors
      }).catch((err) => {
        console.log(err)
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        })
      })
      this.$http.post(this.$domain.domain1 + 'electric-design/getDataOfClassKey')
      .then((res) => {
        this.paymentScale = res.data.payScale
        this.projectNowStates = res.data.projectNowState
      }).catch((err) => {
        console.log(err)
      })
    },
    confirm () {
      var stateUnits = []
      this.form.stateUnits.forEach((el, index) => {
        stateUnits.push({
          'state': el.state,
          'endTime': this.$formDate.formatDate(el.endTime),
          'requireResult': el.requireResult
        })
      })
      var data = {'sourceAccount': this.cookie.get('user'),
        'tenderCompany': this.form.company,
        'changeName': this.form.name,
        'state': this.form.state,
        'sizeCapacityNumber': this.form.sizeAndCapacitys,
        'sizeAndCapacity': this.form.unit,
        'bidType': this.form.character,
        'type': this.form.type,
        'category': this.form.categorys,
        'voltagelevel': this.form.voltagelevel1 + '/' + this.form.voltagelevel2 + '/' + this.form.voltagelevel3,
        'major': this.form.major,
        'address': this.form.place,
        'performanceRequirements': this.form.performanceReq1 + '/:' + this.form.performanceReq2,
        'designProcess': this.form.designState,
        'amountOfInvestment': this.form.lowPrice + '-' + this.form.highPrice,
        'lowestPrice': this.form.lowPrice,
        'highestPrice': this.form.highPrice,
        'instruction': this.form.instruction,
        'requirement': this.form.requirement,
        'payDiscible': this.form.payDiscible,
        'projectNowState': this.form.projectNowState,
        'hasInvoice': this.form.hasInvoice,
        'qualificationRequirements': this.form.aptitude,
        'startTime': this.$formDate.formatDate(this.form.startTime),
        'endTime': this.$formDate.formatDate(this.form.endTime),
        'payMethod': this.form.paymentMethods + '/' + this.form.paymentScale,
        'isAcceptJointBid': this.form.isAcceptJointBid,
        'isJointState': 'true',
        'processRequirements': stateUnits}
      this.$http.post(this.$domain.domain1 + 'electric-design/updateProjectByProjectCode', {'code': this.id, 'data': data}).then((res) => {
        if (res.data.result) {
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: `,${res.data.reason}`,
            type: 'warning'
          })
        }
      }).catch((err) => { console.log(err) })
    }
  },
  mounted () {
    this.initData()
    this.initPremiss()
  },
  watch: {
    radio () {
      if (this.radio === '1') {
        this.disabled = true
      } else {
        this.disabled = false
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.alter{
      width:90%;
  margin:1rem auto;
}
.title0{
  border-bottom:1px solid black;
  text-align:left;
}
.skip{
  margin-top:1rem;
  display:flex;
  justify-content:space-between;
  align-items:center;
  color:#4d83e7;
  font-weight:500;
}
.title{
  display:flex;
  justify-content:flex-start;
  width:100%;
  margin:3rem auto 0 auto;
  font-weight:400;
  font-size:1.5rem;
  border-bottom: 1px solid black;
}
.content{
  margin:3rem auto 0 auto;
  width:100%;
  display:flex;
}
.help{
  margin-left:1rem;color:#409EFF
}
</style>



